* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  padding: 20px 10px;
  background: #e5e5e5;
  font-family: "Roboto", sans-serif;
  color: #3a3a3a;
}

iframe {
  width: auto;
  height: 400px;
}

h1, h2, h3 {
  margin-bottom: 20px;
}

p {
  margin-bottom: 15px;
}
